@import url(https://fonts.googleapis.com/css2?family=Arimo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=BioRhyme:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{background:#3c6b8f;background:radial-gradient(circle, #3c6b8f 0%, #022e51 90%);height:100%;width:100%}h3,h2,h4,h5{font-family:'BioRhyme', serif;font-weight:700}body{margin:0;padding:0;font-family:'Arimo', sans-serif}.App{text-align:center}

.status-font{color:white}.mag-hand-static,.mag-hand-search{opacity:0.5;position:relative;height:25em;z-index:-1;overflow:hidden;transform-origin:left bottom}.mag-hand-search{animation:2s ease loading infinite}.search-results{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around;align-content:center}.matches-num{font-size:0.5em;font-style:italic}@keyframes loading{33%{transform:rotate(8deg)}66%{transform:translateX(15px) translateX(15px) rotate(4deg)}}

.game-image{width:20em;height:auto;justify-content:center;align-content:center;border:#cc6d3d 2px solid}.search-title{color:white;font-size:30px}.game-preview{margin:2em;width:auto;height:auto;padding:1.5em;background-color:rgba(163,180,200,0.6);border-top-left-radius:3em;border-bottom-right-radius:3em;transition:ease 0.25s}.game-preview:hover{background-color:rgba(163,180,200,0.75);box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22)}.preview-info{color:white}.preview-link{text-decoration:none}.game-title{font-size:1.25em;color:white}

input:focus::-webkit-input-placeholder{color:transparent}.slider{transform:scale(1.1)}.search-criteria-font{color:white}.search-box{position:absolute;top:50%;left:50%;width:400px;padding:40px;transform:translate(-50%, -50%);background:rgba(0,0,0,0.5);box-sizing:border-box;box-shadow:0 15px 25px rgba(0,0,0,0.6);border-radius:10px;margin-top:2em}.search-box h2{margin:0 0 30px;padding:0;color:#fff;text-align:center}.search-box .user-box{position:relative}.search-box .user-box input{width:100%;padding:10px 0;font-size:16px;color:#fff;margin-bottom:30px;border:none;border-bottom:1px solid #fff;outline:none;background:transparent}.search-box .user-box label{position:absolute;top:0;left:0;padding:10px 0;font-size:16px;color:#fff;pointer-events:none;transition:0.5s}.search-box .user-box input:focus ~ label,.search-box .user-box input:valid ~ label{top:-20px;left:0;color:#03e9f4;font-size:12px}.search-box form a{position:relative;display:inline-block;padding:10px 20px;color:#03e9f4;font-size:16px;text-decoration:none;text-transform:uppercase;overflow:hidden;transition:0.5s;margin-top:40px;letter-spacing:4px}.search-box a:hover{background:#03e9f4;color:#fff;border-radius:5px;box-shadow:0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4, 0 0 100px #03e9f4}.search-box a span{position:absolute;display:block}.search-box a span:nth-child(1){top:0;left:-100%;width:100%;height:2px;background:linear-gradient(90deg, transparent, #03e9f4);animation:btn-anim1 1s linear infinite}.search-box a span:nth-child(2){top:-100%;right:0;width:2px;height:100%;background:linear-gradient(180deg, transparent, #03e9f4);animation:btn-anim2 1s linear infinite;animation-delay:0.25s}.search-box a span:nth-child(3){bottom:0;right:-100%;width:100%;height:2px;background:linear-gradient(270deg, transparent, #03e9f4);animation:btn-anim3 1s linear infinite;animation-delay:0.5s}.search-box a span:nth-child(4){bottom:-100%;left:0;width:2px;height:100%;background:linear-gradient(360deg, transparent, #03e9f4);animation:btn-anim4 1s linear infinite;animation-delay:0.75s}.mag-hand-search-form{position:fixed;left:-8em;bottom:-3em;height:25em;z-index:-1;overflow:hidden;transform-origin:2em 20em;opacity:0.75;animation:3.5s ease search}.values-display{display:flex;justify-content:space-between}.value{color:#03e9f4}@keyframes search{25%{transform:rotate(10deg) scale(1.1)}50%{transform:rotate(-10deg) scale(1.2)}100%{transform:rotate(-15) scale(1)}}@keyframes btn-anim4{0%{bottom:-100%}50%,100%{bottom:100%}}@keyframes btn-anim3{0%{right:-100%}50%,100%{right:100%}}@keyframes btn-anim2{0%{top:-100%}50%,100%{top:100%}}@keyframes btn-anim1{0%{left:-100%}50%,100%{left:100%}}

.remove-link{text-decoration:none;color:white}.mag-hand-home{position:fixed;left:-8em;bottom:-3em;height:25em;z-index:-1;overflow:hidden;opacity:0.75}.title{color:white;top:60px;left:-10px;position:relative;transform:rotate(-45deg);text-shadow:#022e51 0px 0px 5px;font-size:1.25em}.list-wrapper{position:absolute;top:53%;left:50%;transform:translate(-50%, -50%);margin:0;padding:0;width:600px;height:150px}.list-wrapper li.search-four-player{background-size:300px;background-image:url(/game-sleuth/static/media/darts-image.4e85bc31.jpg);bottom:0;left:25%}.list-wrapper li.search-trending{background-size:300px, 300px;background-image:url(/game-sleuth/static/media/scrable-image.07188534.jpg);top:0;left:50%}.list-wrapper li.search-top{background:#e8e8e8;background:radial-gradient(circle, rgba(232,232,232,0.25) 0%, #a3b4c8 96%);bottom:-1.75em;left:75%;transform:rotate(45deg) scale(1.2)}.list-wrapper li.search-two-player{background-repeat:no-repeat;background-size:300px, 300px;background-image:url(/game-sleuth/static/media/gunslinger-image.d15f5a99.jpg);background-color:#cc6d3d;top:0;left:100%}.list-wrapper li.search-form{background:#cc6d3d;background:radial-gradient(circle, #cc6d3d 0%, #b74803 90%);transform:scale(1.5) rotate(45deg);bottom:14em}.list-wrapper li{border:1px solid #b74803;list-style:none;position:absolute;width:200px;height:200px;background:#000;margin:-100px;transform:rotate(45deg);transition:0.5s;overflow:hidden;opacity:0.8}.list-wrapper li:hover{opacity:1}

.solo-view-wrapper{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%;width:100%;margin:0;background-position:center;background-attachment:fixed;background-repeat:no-repeat;background-size:cover;background-image:url(/game-sleuth/static/media/game-background.89e8a514.jpg)}.view-backdrop{background:rgba(255,255,255,0.9);margin:2em;display:flex;flex-direction:column;align-items:center;justify-content:center;border:#b74803 1px solid}.solo-game-overview{width:100%;display:flex;flex-direction:row}.solo-game-image{height:auto;width:30em;box-shadow:0px 0px 35px 25px #ffffff}.solo-image-wrapper{display:flex;align-items:center;justify-content:center;margin:3em;width:50%}.solo-game-details{display:flex;flex-direction:column;align-items:flex-start;justify-content:center;width:50%;margin:3em}.solo-game-description{text-align:start;margin:2em;padding:0 4em}.solo-sub-details{width:80%;padding:1em;background-color:#022e51;color:white}.solo-game-title{border-bottom:2px #cc6d3d solid;font-size:4em;width:70%;text-align:start}.solo-description-wrapper{width:100%;display:flex}.solo-desc-background{width:65%;margin:2em 3em}.solo-loading-text{margin-top:40vh;color:white}.links-section{display:flex;margin:3em 0;flex-direction:column;align-items:flex-start;background:rgba(204,109,61,0.5);box-shadow:0px 10px 13px -7px #000000;border-radius:1em}.info-link{padding:1em}

.nav-styling{display:flex;justify-content:center;align-items:center;position:-webkit-sticky;position:sticky}.sleuth-img{height:4em;width:4em;margin:1em;cursor:pointer;-webkit-filter:invert;filter:invert}.sleuth:hover,.sleuth-link:hover{-webkit-filter:invert(1);filter:invert(1)}.sleuth:active,.sleuth-link:active{transform:translateY(4px)}.sleuth-div{width:100%;display:flex;align-items:center}.sleuth-link{border-bottom:#cc6d3d 2px solid;transition:0.5s ease all;text-decoration:none}.sleuth-link:visited{text-decoration:none;color:white}

